@import "../common/syles/sass/variables.scss";
@import "../common/syles/sass/mixins";

.nav {
  display: flex;
  width: 350px;
  justify-content: space-around;

  a {
    @extend TitleItem;

    &:hover {
      cursor: pointer;
      color: $textColor
    }
  }

  .active {
    color: $textColor;
  }
}
